	module.exports = {"data":{"markdownRemark":{"html":"<p>Fire sanger opus 2 ble skrevet i studietiden i Leipzig. Edvard Griegs første offentlige konsert i Norge fant sted i Arbeiderforeningen store sal i Bergen 21. mai 1862. På programmet stod blant annet tre av stykkene fra opus 1, og sangene som ble utgitt som opus 2, med sangerinnen Wibecke Meyer. «…som komponist synes herr Grieg å ha en stor fremtid i vente», meldte Bergensposten to dager etter.</p>","timeToRead":1,"excerpt":"Fire sanger opus 2 ble skrevet i studietiden i Leipzig. Edvard Griegs første offentlige konsert i Norge fant sted i Arbeiderforeningen store…","id":"/Users/thomasheimstad/Sites/Obo/src/posts/opus2.md absPath of file >>> MarkdownRemark","frontmatter":{"workname":"Fire sanger","dedicatedTo":"Wibecke Meyer","artists":["Test Testesen","Kari Dahl Nielsen"],"medialink":null,"composedin":"1861","title":"2","author":"Jack Jackson","date":"2017-11-28T20:45:39.703Z","category":"opus","tags":["sang","piano"],"thumbnail":{"childImageSharp":{"sizes":{"base64":"data:image/jpeg;base64,","aspectRatio":1.985522233712513,"src":"/static/BergenColourCrop-bdbf09e827f8528e2946340299d04c0b-47774.jpg","srcSet":"/static/BergenColourCrop-bdbf09e827f8528e2946340299d04c0b-45ad6.jpg 480w,\n/static/BergenColourCrop-bdbf09e827f8528e2946340299d04c0b-e2fcf.jpg 960w,\n/static/BergenColourCrop-bdbf09e827f8528e2946340299d04c0b-47774.jpg 1920w","sizes":"(max-width: 1920px) 100vw, 1920px"}}}},"fields":{"slug":"/opus2/","artist":null,"opuses":null}}},"pathContext":{"slug":"/opus2/"}}

